var Deactivates, Key, Keys, PressedKeys, React, RestartKey;

React = require('react');

Keys = require('./types');

Key = require('./key');

Deactivates = require('./deactivates');

PressedKeys = require('../../stores').PressedKeys;

RestartKey = React.createClass({
  mixins: [Deactivates],
  propTypes: {
    show_label: React.PropTypes.bool
  },
  statics: {
    label: '(restart)'
  },
  getDefaultProps: function() {
    return {
      show_label: true
    };
  },
  getInitialState: function() {
    return {
      active: false
    };
  },
  componentWillMount: function() {
    return PressedKeys.add_change_listener((function(_this) {
      return function(arg) {
        var keycode;
        keycode = arg.keycode;
        return _this._on_change({
          keycode: keycode
        });
      };
    })(this));
  },
  _should_deactivate: function() {
    return this.state.active === true;
  },
  _deactivate: function() {
    return this.setState({
      active: false
    });
  },
  _on_change: function(arg) {
    var keycode;
    keycode = arg.keycode;
    if (Keys.R.keycode() === keycode) {
      return this.setState({
        active: true
      });
    }
  },
  render: function() {
    var label, props;
    label = ((this.props.show_label != null) && this.constructor.label) || null;
    props = {
      pressed: this.state.active,
      keytype: Keys.R,
      label: label
    };
    return React.createElement(Key, React.__spread({}, props));
  }
});

module.exports = RestartKey;
