var ComponentWrapper, React;

React = require('react');

ComponentWrapper = (function() {
  function ComponentWrapper(_component) {
    this._component = _component;
    this._rendered = false;
  }

  ComponentWrapper.wrap = function(component) {
    if (!React.isValidElement(component)) {
      throw new Error("Expected ReactComponent, got " + (component.toString()));
    }
    return new ComponentWrapper(component);
  };

  Object.defineProperty(ComponentWrapper.prototype, '_html_element', {
    enumerable: false,
    writable: true
  });

  ComponentWrapper.prototype.mount = function(html_element) {
    if (!(html_element instanceof HTMLElement)) {
      throw new Error((html_element.toString()) + " isn't an HTMLElement");
    }
    this._html_element = html_element;
    return this;
  };

  ComponentWrapper.prototype.render = function() {
    if (this._html_element == null) {
      throw new Error(".mount(HTMLElement html_element) before rendering!");
    } else if (this._rendered) {
      throw new Error("Component's already been rendered!");
    }
    React.render(this._component, this._html_element);
    this._rendered = true;
    return this;
  };

  return ComponentWrapper;

})();

module.exports = {
  wrap: ComponentWrapper.wrap.bind(ComponentWrapper)
};
