var GRID, Grid, GridEvolver, React, Row, wrap;

React = require('react');

Row = require('../row');

GRID = require('../../settings').GRID;

GridEvolver = require('../../stores').GridEvolver;

Grid = React.createClass({
  getInitialState: function() {
    return {
      cellmap: GridEvolver.cellmap()
    };
  },
  componentDidMount: function() {
    return GridEvolver.add_cells_listener(this._on_change);
  },
  _on_change: function() {
    return this.setState({
      cellmap: GridEvolver.cellmap()
    });
  },
  _get_row_cells: function(row) {
    return this.state.cellmap.filter(function(cell, xy) {
      return xy.x === row;
    }).valueSeq().toJS();
  },
  render: function() {
    var row;
    return React.createElement("div", {
      "className": "grid"
    }, (function() {
      var i, len, ref, results;
      ref = GRID.range();
      results = [];
      for (i = 0, len = ref.length; i < len; i++) {
        row = ref[i];
        results.push(React.createElement(Row, {
          "cells": this._get_row_cells(row),
          "index": row,
          "key": "row-" + row
        }));
      }
      return results;
    }).call(this));
  }
});

wrap = require('../wrapper').wrap;

module.exports = wrap(React.createElement(Grid, null));
