var CELL_QUANTITIES, Cells, Immutable, _;

Immutable = require('immutable');

_ = require('underscore');

Cells = require('../components/cell/types');

CELL_QUANTITIES = Immutable.Map([[Cells.WALL, 15], [Cells.ITEM, 5]]);

module.exports = Object.create(null, {
  rounds_to_win: {
    enumerable: true,
    value: 4
  },
  max_score: {
    enumerable: true,
    value: function() {
      return (CELL_QUANTITIES.get(Cells.ITEM)) * this.rounds_to_win;
    }
  },
  get_cell_quantity: {
    enumerable: true,
    value: function(cell) {
      return CELL_QUANTITIES.get(cell);
    }
  }
});
